body {
  background-color: #3851bc;color: #fff;font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.App {
  max-width: 800px;
  margin: 0 auto;
}

.App h1 {
  text-align: center;
  font-weight: 100;
  border-bottom: 1px solid #2539a0;
  padding: 25px;
}

.App input[type=search] {
  width: 100%;
}

.App input[type=submit] {
  width: 130px;
  text-align: center
}

.App input {
  border: none;
  padding: 15px;
  font-size: 18px;
  font-weight: 100;
  padding-right: 130px;
}

.App {
  font-weight: 100;
}

.App input[type=submit] {
  background: #000;
  color: #fff;
  position: absolute;
  right: 0;
  top: 0;
  text-align: center
}

.App form {
  width: 400px;
  margin: 0 auto;
  position: relative;
}

input:focus {
  outline: none;
}

p.instructions, p.loading, p.warning {
  background: #fff3cd;
  padding: 10px 30px;
  text-align: center;
  color: #856504;
  border-radius: 2px;
  margin-top: 30px;
}

p.loading {
  background: #d4edda;
  color: #1f5d2c
}

p.warning {
  background: #f8d7da;
  color: #721c23
}

div#loading-wrapper {
  color: #333;
}

div#loading-wrapper h3 {
  font-weight: 100;
  margin-top: 40px;
  font-size: 25px;
  color: #fff;
  text-align: center;
}

div#gist-embed {
  margin-top: 25px;
}
div#single-gist {
  margin-top: 20px;
  margin-left: -8px;
  margin-right: -8px;
}